.pointer {
  position: fixed;
  width: px2vw(58);
  height: px2vw(58);
  pointer-events: none;
  user-select: none;
  top: 0;
  left: 0;
  z-index: 10;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;

  &-center {
    position: absolute;
    width: px2vw(6);
    height: px2vw(6);
    border-radius: 50%;
    background: red;
  }

  svg {
    overflow: visible;
    position: absolute;
    top: 0;
    width: 100%;
    height: px2vw(50);
  }

  &.hidden {
    display: none;
  }
}
