section.contacts {
  background: $white;
  padding-top: px2vw(100);
  padding-left: 0;

  .contacts-header {
    padding-left: px2vw(390);
    padding-right: px2vw(225);

    &__title {
      margin-bottom: px2vw(20);
    }

    &__info {
      display: flex;
      justify-content: space-between;

      .links {
        a {
          display: inline-block;
          @include title;
          font-size: px2vw(22);
          font-weight: 700;
          text-decoration: none;
          line-height: 110%;
          transition: 0.3s ease;
        }

        .phone {
          color: $dark;
          &:hover {
            color: $dark2;
          }
        }

        .email {
          color: $red;
          &:hover {
            color: $dark2;
          }
        }
      }

      .address {
        margin-top: -0.25em;
      }

      .copyright {
        margin-top: px2vw(35);
        color: $blue-grey;
        @include text;
        opacity: 0.5;
      }
    }
  }

  .contacts-body {
    display: flex;

    .contacts-image {
      transform: translateY(px2vw(70));
      width: px2vw(950);
      height: px2vw(636);
      @include center-image;
    }

    .contacts-map {
      width: px2vw(970);
      height: px2vw(636);
      background-color: gray;
    }
  }

  .contacts-footer {
    display: flex;
    align-items: center;
    height: px2vw(210);
    background-color: $dark2;
    padding-left: px2vw(995);

    .createdby {
      @include text;
      color: $blue-grey;

      a {
        @include text;
        text-decoration: none;
        color: $blue-grey;
        opacity: 0.8;
        transition: all 0.3s ease;
      }

      &:hover {
        a {
          opacity: 1;
          color: $white;
        }
      }
    }
  }
}
