
@mixin title {
  font-family: "Blender Pro", sans-serif;
  margin: 0;

  span {
    color: $red;
  }
}

@mixin text {
  font-family: "Roboto-Regular", sans-serif;
  font-size: px2vw(16);
  line-height: 160%;
  letter-spacing: 0.03em;
}

@mixin text-medium {
  font-family: "Roboto-Medium", sans-serif;
  font-size: px2vw(16);
  line-height: 160%;
  letter-spacing: 0.03em;
}

h1,
h2,
h3,
h4,
h5 {
  @include title;
}

h1 {
  font-size: px2vw(60);
  line-height: px2vw(56);
  text-transform: uppercase;
}

h2 {
  font-size: px2vw(34);
  line-height: 93%;
  text-transform: uppercase;
}

p.regular {
  @include text;
}

p.medium {
  @include text-medium;
}