section.about {
  background: $white;
  z-index: 2;
  height: auto;
  padding-top: px2vw(137);

  .title-block {
    padding-bottom: px2vw(650);
  }

  .image-block {
    position: absolute;
    width: px2vw(1160);
    height: px2vw(734);
    top: px2vw(280);
    // top: px2vw(145);
    left: px2vw(390);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    .text {
      flex: none;
      p {
        width: px2vw(430);
        color: $blue-grey;
        margin-top: -0.3em;
      }
    }

    .image {
      flex: none;
      @include center-image;

      &.image1 {
        width: px2vw(435);
        height: px2vw(294);
      }

      &.image2 {
        width: px2vw(725);
        height: px2vw(440);
      }
    }
  }
}
