.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: px2vw(124);
  height: px2vw(124);
  border: px2vw(1) solid $red;
  border-radius: 50%;
  font-family: "Blender Pro", sans-serif;
  color: $white;
  font-size: px2vw(18);
  text-decoration: none;
  transition: 0.3s ease;
  // box-shadow: 0 0 px2vw(1) px2vw(2) transparent;

  &:hover {
    border-color: $white;
    // box-shadow: 0 0 px2vw(1) px2vw(2) $red;
    // color: $red;
  }
}
