.contacts-footer .copyright {
    display: none;
  }

.social-icons {
  position: fixed;
  z-index: 5;
  bottom: px2vw(100);
  left: px2vw(100);
  display: flex;
  flex-direction: column;

  a {
    z-index: 2;
    width: px2vw(26);
    height: px2vw(26);
    @include svg_icon;
    margin-top: px2vw(20);
    opacity: 0.6;
    transition: opacity 0.3s ease;

    svg path {
      fill: #7b7d89;
      transition: fill 0.3s ease;
    }

    &:hover {
      opacity: 1;

      // svg path {
      //   fill: $white;
      // }
    }
  }
}

.video-popup {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  z-index: 2;
  visibility: hidden;

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background: $dark2;
    opacity: 0.84;
    z-index: -1;
  }

  .video-block {
    display: flex;
    justify-content: flex-start;
    position: absolute;
    top: px2vw(150);
    left: px2vw(378);
    width: px2vw(1220);
    height: px2vw(660);

    video {
      width: px2vw(1140);
      height: px2vw(660);
      cursor: auto !important;
    }

    .close-popup {
      width: px2vw(60);
      height: px2vw(60);
      margin-left: px2vw(20);
      margin-top: px2vw(10);
      @include svg-icon;
      svg {
        path,
        rect {
          transition: all 0.3s ease;
        }
      }

      &:hover {
        path {
          fill: $white;
        }

        rect {
          stroke: $white;
        }
      }
    }
  }
}
