@mixin soldier {
  position: absolute;
  right: 0;
  bottom: -3%;
  width: px2vw(1070);
  height: px2vw(960);
  // pointer-events: none;

  & > .hud {
    position: absolute;
    @include svg-icon;

    .hud-point {
      width: px2vw(18);
      height: px2vw(18);
      border: px2vw(1) solid $white;
      position: absolute;
      transform: rotate(45deg) scale(5);
      opacity: 0;
    }

    .hud-title {
      position: absolute;
      display: flex;
      align-items: center;

      .text {
        font-size: px2vw(16);
        color: $white;
        @include title;
        span {
          color: $red;
        }
      }

      .icon {
        margin-left: px2vw(8);
        width: px2vw(20);
        height: px2vw(20);
        opacity: 0;
      }
    }

    .hud-desc {
      position: absolute;
      overflow: hidden;

      & > div {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        transform: translateX(-100%);
        opacity: 0;
      }

      &.large {
        width: px2vw(74);
        height: px2vw(29);
        & > div {
          background: url("../img/hud/hud-desc-large.png") no-repeat top center /
            cover;
        }
      }
    }

    // HEALTH HUD
    &.health-hud {
      width: px2vw(244);
      height: px2vw(157);
      bottom: 45%;
      left: 0%;

      .hud-point {
        bottom: 0;
        right: 0;
      }

      .hud-title {
        top: 0;
        left: 0;
      }

      .hud-desc {
        top: 25%;
        left: 0;
      }
    }

    // CONNECTION HUD
    &.connect-hud {
      width: px2vw(228);
      height: px2vw(140);
      bottom: 71%;
      left: 16%;

      .hud-title {
        top: 0;
        left: 0;
      }

      .hud-point {
        bottom: px2vw(8);
        right: px2vw(8);
      }

      .hud-desc {
        top: 28%;
        left: 0;
      }
    }

    // MAP 3D HUD
    &.map3d-hud {
      width: px2vw(189);
      height: px2vw(140);
      top: 20%;
      left: 49.5%;

      .hud-title {
        top: 10%;
        left: 56%;
      }

      .hud-point {
        bottom: px2vw(14);
        left: px2vw(5);
      }

      .hud-desc {
        top: 36%;
        left: 55%;
      }
    }

    // NAVIGATION HUD
    &.navigation-hud {
      width: px2vw(400);
      height: px2vw(278);
      bottom: 26.5%;
      left: 37%;

      .hud-title {
        &.left {
          top: 0;
          left: 0;
        }

        &.right {
          top: 76%;
          left: 84%;
        }
      }

      .hud-point {
        top: 57%;
        left: 63%;
      }

      .hud-desc {
        &.left {
          height: px2vw(15);
          top: 14%;
          left: 0;
        }

        &.right {
          top: 89%;
          left: 84%;
        }
      }

      .compass {
        position: absolute;
        width: px2vw(136);
        height: px2vw(112);
        top: 20%;
        left: 0;
        opacity: 0;
        transform: translateY(-10%);
      }
    }

    // FLYING TERRAIN
    &.terrain {
      position: absolute;
      width: px2vw(562);
      height: px2vw(317);
      right: 0;
      bottom: 39%;
      background: url("../img/hud/terrain.png") no-repeat center center / cover;
      opacity: 0;
      transform: translateY(5%);
    }

    &.terrain-top-hud {
      width: px2vw(118);
      height: px2vw(53);
      bottom: 71%;
      left: 75%;
      opacity: 0;
      transform: translateY(-20%);
    }

    &.terrain-bottom-hud {
      width: px2vw(320);
      height: px2vw(163);
      bottom: 50.5%;
      left: 67%;
      opacity: 0;
      transform: translateY(-10%);
    }
  }

  .person {
    position: absolute;
    bottom: 0;
    left: 0;
    width: px2vw(680);
    height: px2vw(796);
    right: px2vw(400);
    background: url("../img/bg/soldier.png") no-repeat center center / cover;
  }
}
