section.intro {
  height: 100vh;
  padding-top: px2vw(220);
  padding-bottom: px2vw(65);
  overflow: hidden;

  .intro-bg {
    position: absolute;
    width: 105%;
    height: 105%;
    top: -1.5%;
    left: -1.5%;
    background: $dark2 url('../img/bg/intro-bg.jpg') center center / cover;
    z-index: -1;

    &-noise {
      position: absolute;
      width: 105%;
      height: 105%;
      top: -1.5%;
      left: -1.5%;
      background: url('../img/bg/intro-bg-noise.png') center center / cover;
      // mix-blend-mode: screen;
      mix-blend-mode: soft-light;
      pointer-events: none;

      &::after {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background: url('../img/bg/intro-bg-lighten.png') center center / cover;
        mix-blend-mode: color-dodge;
      }
    }
  }

  .turret {
    position: absolute;
    width: px2vw(182);
    height: px2vw(136);
    bottom: px2vw(100);
    left: px2vw(500);
    background: url('../img/bg/turret.png') no-repeat center center / cover;
    pointer-events: none;
    z-index: -1;
  }

  .soldier {
    @include soldier;
  }

  .bullet-back {
    position: absolute;
    width: px2vw(324);
    height: px2vw(37);
    bottom: px2vw(200);
    left: px2vw(1200);
    background: url('../img/bg/bullet-back.png') no-repeat center center / cover;
    pointer-events: none;
    mix-blend-mode: screen;
  }

  .bullet-front {
    position: absolute;
    width: px2vw(439);
    height: px2vw(102);
    bottom: px2vw(90);
    right: px2vw(680);
    background: url('../img/bg/bullet-front.png') no-repeat center center / cover;
    pointer-events: none;
    mix-blend-mode: screen;
  }

  .red-light {
    position: absolute;
    width: px2vw(1023);
    height: 100%;
    bottom: 0;
    right: 0;
    background: url('../img/bg/red-light.png') no-repeat center center / cover;
    mix-blend-mode: hard-light;
    pointer-events: none;
  }

  .grass {
    position: absolute;
    width: 100%;
    height: px2vw(340);
    bottom: -3%;
    right: 0;
    background: url('../img/bg/grass.png') no-repeat center center / cover;
    pointer-events: none;
    mix-blend-mode: screen;
  }

  .text-block {
    width: px2vw(380);
    color: $white;

    .stripe {
      @include text-medium;
      display: inline-block;
      background: $red;
      font-weight: bolder;
      font-size: px2vw(12);
      padding: px2vw(8) px2vw(14);
      text-transform: uppercase;
      color: $dark2;
      margin-bottom: px2vw(20);
    }

    h1 {
      margin-bottom: px2vw(30);
    }

    p {
      margin-bottom: px2vw(55);
      opacity: .5;
    }
  }

  .image-block {
    flex: none;
    margin-top: px2vw(94);
    margin-left: px2vw(30);
    width: px2vw(917);
    height: px2vw(794);
    @include center-image;
  }
}