section.gallery {
  height: px2vw(565);
  padding-left: 0;
  background: $dark2;
  z-index: 2;

  .left-slider {
    width: 50%;
    height: px2vw(636);
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    .dark-slider {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 0;

      &__overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: $dark2;
        opacity: 0.85;
      }

      img {
        filter: blur(5px);
      }
    }

    .title-block {
      width: px2vw(270);
      z-index: 1;

      h2 {
        color: $white;
      }

      p {
        margin-top: px2vw(20);
        color: $blue-grey;
      }
    }

    .slides-nav {
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: px2vw(560);

      &__button {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: px2vw(54);
        height: px2vw(54);
        border: px2vw(1) solid $blue-grey;
        border-radius: 50%;
        transition: border-color 0.3s ease;

        &.next-slide svg {
          transform: rotate(90deg);
        }

        &.prev-slide svg {
          transform: rotate(-90deg);
        }

        svg {
          height: px2vw(15);
          width: px2vw(10);
        }

        &:hover {
          border-color: $red;
        }
      }
    }
  }

  .right-slider {
    width: 50%;
    height: px2vw(636);
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .swiper-slide {
    width: 100%;
    height: auto;
    @include center-image;
  }
}
