section.team {
  background: $dark2;
  min-height: 100vh;
  padding-top: px2vw(225);
  padding-bottom: px2vw(225);

  .title-block {
    color: $white;
  }

  .team-block {
    width: px2vw(1140);
    margin-top: px2vw(120);
    // border: 1px solid red;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-row-gap: px2vw(110);
    grid-column-gap: px2vw(160);
    // display: flex;
    // justify-content: space-between;
    // flex-wrap: wrap;

    .member {
      // flex: none;
      width: px2vw(270);

      .image {
        width: 100%;
        height: px2vw(270);
        @include center-image;
        margin-bottom: px2vw(25);
      }

      h2 {
        color: $red;
      }

      p {
        margin-top: px2vw(10);
        margin-bottom: px2vw(15);
        color: $blue-grey;
        transition: color 0.6s ease;
      }

      a {
        @include text;
        color: $blue-grey;
        text-decoration: none;
        opacity: 0.8;
        transition: all 0.3s ease;
      }

      &:hover {
        a {
          color: $red;
          opacity: 1;

          &:hover {
            color: $white;
          }
        }

        p {
          color: $white;
        }
      }
    }
  }
}
