section.facts {
  background: $white;
  padding-top: px2vw(120);
  padding-bottom: px2vw(200);
  padding-right: px2vw(225);

  .title-block {
    color: $dark;

    p {
      margin-top: px2vw(30);
      width: px2vw(560);
    }
  }

  .facts-wrapper {
    display: block;
    width: px2vw(1310);
    margin-top: px2vw(60);
    overflow: hidden;
    padding-left: px2vw(72.5);
    padding-right: px2vw(72.5);
    padding-top: px2vw(30);
    padding-bottom: px2vw(30);
  }

  .facts-blocks {
    display: flex;
    margin-left: px2vw(-145);
    margin-right: px2vw(-145);
    margin-top: px2vw(-60);
    margin-bottom: px2vw(-60);
    flex-wrap: wrap;

    .block {
      flex: none;
      width: px2vw(145);
      margin-right: px2vw(72.5);
      margin-left: px2vw(72.5);
      margin-top: px2vw(30);
      margin-bottom: px2vw(30);

      .title {
        color: $red;
        @include title;
        font-size: px2vw(60);
      }
      p {
        color: $dark;
      }
    }
  }
}
