header {
  position: fixed;
  z-index: 5;
  width: 100vw;
  height: px2vw(94);
  display: flex;
  align-items: center;
  padding-left: px2vw(100);
  padding-right: px2vw(100);

  .header-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    // background: rgba(13, 16, 27, .8);

    // @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
    //   background: rgba(13, 16, 27, .4);
    //   backdrop-filter: blur(200px);
    // }
  }

  .logo {
    position: relative;
    width: px2vw(54);
    height: px2vw(54);
    @include svg-icon;
  }

  .desktop-menu {
    width: 100%;
    margin-left: px2vw(240);
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 5;

    nav {
      a {
        font-family: "Roboto-Medium", sans-serif;
        font-size: px2vw(16);
        line-height: 150%;
        color: $white;
        text-decoration: none;
        margin-right: px2vw(45);
        transition: color 0.3s ease;

        &:hover,
        &.active {
          color: $red;
        }

        &.active {
          pointer-events: none;
        }
      }
    }
  }

  .mobile-menu,
  .burger {
    display: none;
  }

  &.inverted {
    nav a {
      color: $dark2;
    }
  }
}
