// Convert px to vw
@function px2vw($px) {
  // Default viewport width
  $def_screen: 1920;
  $vw: $def_screen * 0.01;
  @return ($px / $vw) * 1vw;
}

// Image fit & center
@mixin center-image {
  position: relative;
  img {
    position: absolute;
    object-fit: cover;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
  }
}

@mixin img {
  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

// SVG icons
@mixin svg-icon {
  svg {
    width: 100%;
    height: 100%;
  }
}

// No scroll
@mixin no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
  ::-webkit-scrollbar {
    width: 0;
  }
}
