section.turret {
  min-height: 100vh;
  background: $dark2 url("../img/bg/intro-bg.jpg") no-repeat top center / cover
    fixed;
  background-blend-mode: screen;
  padding-top: px2vw(328);
  padding-bottom: px2vw(140);

  .title-block {
    width: px2vw(1150);
    display: flex;
    justify-content: space-between;
    color: $white;
  }

  .showreel {
    display: flex;
    justify-content: space-between;
    width: px2vw(425);

    &-image {
      flex: none;
      width: px2vw(191);
      height: px2vw(142);
      @include center-image;
    }

    &-control {
      flex: none;
      margin-left: px2vw(30);
      width: px2vw(204);

      .play {
        width: px2vw(42);
        height: px2vw(42);
        margin-bottom: px2vw(15);
        @include svg-icon;
        transition: transform 0.3s ease;
      }
    }

    &:hover {
      .play {
        transform: scale(1.25);
      }
    }
  }

  .turret-blocks {
    width: px2vw(1140);
    margin-top: px2vw(80);

    .block {
      display: flex;
      align-items: center;
      color: $white;

      .image {
        flex: none;
        width: px2vw(560);
        height: px2vw(560);
        display: flex;
        align-items: center;
        justify-content: center;

        img {
          width: 100%;
          height: 100%;
          object-fit: contain;

        }
      }

      .desc {
        width: px2vw(350);
        p {
          margin-top: px2vw(25);
        }
      }

      // Right image
      &:nth-of-type(even) {
        flex-direction: row-reverse;

        .desc {
          margin-right: px2vw(200);
        }
      }

      // Left image
      &:nth-of-type(odd) {
        flex-direction: row;

        .desc {
          margin-left: px2vw(165);
        }
      }
    }
  }
}
